/* eslint-disable react/no-array-index-key */
import { Box } from '@mui/material';
import type { FC, ReactNode } from 'react';
import { useId } from 'react';
import LegalTextListItem from './LegalTextListItem';
export interface LegalTextProps {
  readonly text: Array<{
    bulletPoints?: Array<Array<string> | string>;
    title: string;
    subtitle?: string;
  }>;
  readonly componentReplace?: Record<string, ReactNode>;
}
const LegalText: FC<LegalTextProps> = ({
  text,
  componentReplace
}) => {
  const id = useId();
  return <Box display="flex" flexDirection="column" gap={{
    sm: 3,
    xs: 1.5
  }} data-sentry-element="Box" data-sentry-component="LegalText" data-sentry-source-file="LegalText.tsx">
      {text.map((textSection, index) => <LegalTextListItem key={`${id}-${index}`} bulletPoints={textSection.bulletPoints ?? []} componentReplace={componentReplace} position={index + 1} subtitle={textSection.subtitle} title={textSection.title} />)}
    </Box>;
};
export default LegalText;