'use client';

/* eslint-disable react/jsx-sort-props */
import type { Breakpoint, SxProps, Theme } from '@mui/material/styles';
import { useBoolean } from '@riptech/minimal-ui/src/hooks/use-boolean';
import { HeaderBase } from '../core/header-base';
import { LayoutSection } from '../core/layout-section';
import { CompactContent, Main } from './main';

// ----------------------------------------------------------------------

export interface SimpleLayoutProps {
  readonly sx?: SxProps<Theme>;
  readonly children: React.ReactNode;
  readonly content?: {
    compact?: boolean;
  };
}
export function SimpleLayout({
  sx,
  children,
  content
}: SimpleLayoutProps) {
  const mobileNavOpen = useBoolean();
  const layoutQuery: Breakpoint = 'md';
  return <LayoutSection
  /** **************************************
   * Header
   *************************************** */ headerSection={<HeaderBase routes={[]} layoutQuery={layoutQuery} onOpenNav={mobileNavOpen.onTrue} slotProps={{
    container: {
      maxWidth: false
    }
  }} />}
  /** **************************************
   * Footer
   *************************************** */ sx={sx} footerSection={null}
  /** **************************************
   * Style
   *************************************** */ cssVars={{
    '--layout-simple-content-compact-width': '448px'
  }} data-sentry-element="LayoutSection" data-sentry-component="SimpleLayout" data-sentry-source-file="layout.tsx">
      <Main data-sentry-element="Main" data-sentry-source-file="layout.tsx">{content?.compact ? <CompactContent>{children}</CompactContent> : children}</Main>
    </LayoutSection>;
}